import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue';
export default {
	data() {
		return {
			id:-1,
			order: null,
			act: null,
			ewm: '',
			type:0
		};
	},
	components: { tkiQrcode },
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad(opt) {
		if (opt.id) {
			this.id=opt.id;
			this.$Fetch({
				url: 'activity_list/getDataById',
				data: { id: opt.id },
				success: res => {
					console.log(res);
					this.order = res;
					this.act = res.activity;
					this.$refs.qrcode._makeCode();
					let start=res.activity.start_time.replace(/\-/g,"/");
					let end=res.activity.end_time.replace(/\-/g,"/");
					let now =new Date().getTime()
					if(now<new Date(start).getTime()){
						this.type=0
					}else if(now<=new Date(end).getTime()){
						this.type=1
					}else{
						this.type=2
					}
				}
			});
		}
	},
	methods: {
		qrR(e) {
			console.log(e);
			this.ewm = e;
		},
		previewImage() {
			uni.previewImage({
				urls: [this.ewm]
			});
		},
		doCancal(){
			this.$Fetch({
				url:"activity_list/del",
				data:{id:this.id},
				success:res=>{
					uni.showToast({
						title:"成功取消",
						icon:"success"
					})
					setTimeout(()=>{
						uni.navigateBack({
							
						})
					})
				}
			})
		}
	}
};
